"use client";

import { useState } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

type TimelinePoint = { time: string; soc: number };

type PredictResponse = {
  hours: number;
  final_battery: number;
  timeline: TimelinePoint[];
  start_time: string;
  end_time: string;
  cost_now: number;
  cost_optimized: number;
  savings: number;
};

export default function EVOptimizerPage() {
  const [battery, setBattery] = useState<number>(25);
  const [power, setPower] = useState<number>(7);
  const [departure, setDeparture] = useState<string>("19:00");
  const [cheapMode, setCheapMode] = useState<boolean>(true);

  const [result, setResult] = useState<PredictResponse | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [countdown, setCountdown] = useState<string>("");

  const calculateCountdown = (departureTime: string) => {
    const now = new Date();
    const [h, m] = departureTime.split(":").map(Number);
    const target = new Date();
    target.setHours(h, m, 0, 0);
    if (target <= now) target.setDate(target.getDate() + 1);
    const diff = target.getTime() - now.getTime();
    const hrs = Math.floor(diff / (3600 * 1000));
    const mins = Math.floor((diff % (3600 * 1000)) / 60000);
    return `${hrs}h ${mins}m`;
  };

  const handleOptimize = async () => {
    setLoading(true);
    setResult(null);
    setCountdown(calculateCountdown(departure));

    try {
      const res = await fetch("http://localhost:8000/predict", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          battery_level: battery,
          charger_power: power,
          target_time: departure,
          cheap_mode: cheapMode,
        }),
      });

      if (!res.ok) {
        throw new Error(`Server error ${res.status}`);
      }

      const data: PredictResponse = await res.json();
      setResult(data);
    } catch (err) {
      if (err instanceof Error) {
        alert("Error: " + err.message);
      } else {
        alert("Unknown error");
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-gray-950 text-white p-8">
      <h1 className="text-3xl font-bold mb-6 text-center text-blue-400">
        ‚ö° Smart EV Charging Optimization (Energy AI Demo)
      </h1>

      <div className="max-w-4xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Left - Controls */}
        <div className="bg-gray-900 p-6 rounded-xl shadow-lg space-y-4">
          <div>
            <label className="block text-sm text-gray-400 mb-1">
              Current Battery Level: <span className="font-semibold">{battery}%</span>
            </label>
            <input
              type="range"
              min={0}
              max={100}
              value={battery}
              onChange={(e) => setBattery(Number(e.target.value))}
              className="w-full accent-blue-500"
            />
          </div>

          <div>
            <label className="block text-sm text-gray-400 mb-1">Charger Power (kW)</label>
            <select
              value={power}
              onChange={(e) => setPower(Number(e.target.value))}
              className="bg-gray-800 w-full p-2 rounded-md"
            >
              <option value={3}>3 kW</option>
              <option value={7}>7 kW</option>
              <option value={11}>11 kW</option>
              <option value={22}>22 kW</option>
            </select>
          </div>

          <div>
            <label className="block text-sm text-gray-400 mb-1">Departure Time</label>
            <input
              type="time"
              value={departure}
              onChange={(e) => setDeparture(e.target.value)}
              className="bg-gray-800 w-full p-2 rounded-md"
            />
          </div>

          <label className="flex items-center gap-2">
            <input
              type="checkbox"
              checked={cheapMode}
              onChange={() => setCheapMode(!cheapMode)}
            />
            <span className="text-sm text-gray-300">Optimize for low electricity cost (TNB ToU)</span>
          </label>

          <button
            onClick={handleOptimize}
            disabled={loading}
            className="w-full py-2 rounded-md bg-blue-600 hover:bg-blue-500 font-semibold disabled:opacity-60"
          >
            {loading ? "Calculating..." : "Optimize Charging üî•"}
          </button>

          <div className="text-sm text-gray-400 mt-2">
            <p>Low price window (TNB): 22:00‚Äì02:00 nightly. Weekends and public holidays treated as low-price days.</p>
          </div>
        </div>

        {/* Right - Results */}
        <div className="bg-gray-900 p-6 rounded-xl shadow-lg">
          <h2 className="text-xl font-semibold mb-3">Recommendation</h2>

          {!result && (
            <div className="text-gray-400">No recommendation yet. Set inputs and click Optimize.</div>
          )}

          {result && (
            <>
              <div className="grid grid-cols-1 gap-2">
                <div className="flex justify-between">
                  <div>‚è± Countdown</div>
                  <div className="font-semibold">{countdown}</div>
                </div>

                <div className="flex justify-between">
                  <div>üöó Suggested Charging</div>
                  <div className="font-semibold">{result.hours} hours</div>
                </div>

                <div className="flex justify-between">
                  <div>üîã Final Battery</div>
                  <div className="font-semibold">{result.final_battery}%</div>
                </div>

                <div className="flex justify-between">
                  <div>üí∞ Cost (charge now)</div>
                  <div className="font-semibold">RM {result.cost_now.toFixed(2)}</div>
                </div>

                <div className="flex justify-between">
                  <div>üí∏ Cost (optimized)</div>
                  <div className="font-semibold">RM {result.cost_optimized.toFixed(2)}</div>
                </div>

                <div className="flex justify-between">
                  <div>üéØ Estimated Savings</div>
                  <div className="font-semibold text-green-300">RM {result.savings.toFixed(2)}</div>
                </div>
              </div>

              {/* Chart */}
              <div className="h-56 mt-4">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={result.timeline}>
                    <XAxis dataKey="time" stroke="#7dd3fc" />
                    <YAxis domain={[0, 100]} stroke="#7dd3fc" />
                    <Tooltip />
                    <Line type="monotone" dataKey="soc" stroke="#60a5fa" strokeWidth={2} />
                  </LineChart>
                </ResponsiveContainer>
              </div>

              <div className="text-xs text-gray-400 mt-3">
                Start: {result.start_time} ‚Ä¢ End: {result.end_time}
              </div>
            </>
          )}
        </div>
      </div>
    </main>
  );
}
